@use 'base/variables'  as v;
@use 'base/mixins'  as m;

.video {
    position: relative;
    max-height: 70rem;
    min-height: 50rem;
    overflow: hidden;

.overlay{
    position: absolute;
    background: rgb(141,15,244);
    background: linear-gradient(270deg, rgba(141,15,244,0.5) 0%, rgba(255,0,69,0.3) 100%);
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;

}

 video {
    width: auto;
    display: block;
    @include m.desktop {
        width: 100%;
        
    }
 }
}

.contenido-video {
    z-index: 10;
    h2{ 
     color: v.$blanco;
     font-size: 7rem;
     margin: 0;

    }

    p{
        background-color: v.$rosa;
        display: inline-block;
        padding: 1rem 2rem;
        color: v.$blanco;
        font-weight: 700;
        font-size: 2.4rem;
        margin: 0;
        

    }
}





















































